export default {
  layout: "Adm-content",
  data() {
    return {
      department: {
        department_name: "",

      },
      errors: {},
    };
  },
  methods: {
    adddepartment() {
      this.$axios
        .$post("/admission/department-add", this.department)
        .then((res) => {
          this.department = "";
          this.errors = {};
          this.$toaster.success(res.message);
          this.$router.push("/admission/department");
        })
        .catch((error) => {
          this.errors = error.response.data.errors;
        });
    },
  },
};
